import Vue from 'vue'
import Router from 'vue-router'
//引入页面
//import 引用名 form '@/路径'
import Login from '@/components/login/login'   //登录页面
import Index from '@/components/index/index'   //首页
import Welcome from '@/components/welcome/welcome'   //主页
import Welcome2 from '@/components/welcome2/welcome2'   //主页2
import Message from '@/components/message/message'   //消息
import Warning from '@/components/warning/warning'   //告警与报错
import Warninglog from '@/components/warning/warninglog'   //告警日志
import Devices from '@/components/devices/devices'   //设备管理
import Services from '@/components/servicesmanage/services'   //设备管理
import Servicesinfo from '@/components/servicesmanage/servicesinfo/serviceinfo'   //设备管理
import OID  from '@/components/oid/oid.vue'   //OID管理
import InterFace  from '@/components/interface/interfaceSetting.vue'   //接口管理
import InterFaceView  from '@/components/interface/InterFaceView.vue'   //接口管理
import Thirdparty  from '@/components/thirdparty/thirdparty.vue'   //服务管理
import Warninfo from '@/components/servicesmanage/warninfo/warninfo'   //单个设备详情
import Wappush from '@/components/servicesmanage/warninfo/items/wappush.vue'   //单个服务详情CPU使用率
import Cpuuse from '@/components/servicesmanage/warninfo/items/cpuuse.vue'   //单个服务详情CPU使用率
import Memoryuse from '@/components/servicesmanage/warninfo/items/memoryuse'   //单个服务详情内存使用率
import Connectnumber from '@/components/servicesmanage/warninfo/items/connectnumber'   //单个服务详情连接数
import Servicelog from '@/components/servicesmanage/warninfo/items/servicelog'   //单个服务详情服务日志
import serviceConfig from '@/components/servicesmanage/warninfo/items/serviceConfig'   //单个服务详情服务配置
import Connectfail from '@/components/servicesmanage/warninfo/items/connectfail'   //单个设备详情cpu使用情况
import IPtableInfo from '@/components/servicesmanage/iptableinfo/iptableinfo.vue'   //单个设备详情防火墙
import EsInfo from '@/components/servicesmanage/esinfo/esinfo.vue'   //单个设备详情ES
import ES from '@/components/servicesmanage/esinfo/items/es.vue'   //单个设备详情EStable
// import EsInfo from '@components/servicesmanage/esInfo/esinfo.vue'
import IP from '@/components/servicesmanage/iptableinfo/items/IP.vue'   //单个设备详情防火墙端口规则
import IPrule from '@/components/servicesmanage/iptableinfo/items/IPrule.vue'   //单个设备详情防火墙IP规则
import Custom from '@/components/custom/custom' // 人员管理
import Word from '@/components/word/word' // 字冠设置
import Firewall from '@/components/firewall/firewall' // 防火墙
import Params from '@/components/sysparms/sysparams' // 系统参数
import Allowance from '@/components/allowance/index.vue' // 余量


//解决重复路由报错问题
const originalReplace = Router.prototype.replace
Router.prototype.replace = function replace(location) {
  return originalReplace.call(this, location).catch(err => err)
}
let routerPush = Router.prototype.push;
Router.prototype.push = function push(location) {
  return routerPush.call(this, location).catch(err => err)
}
Vue.use(Router)
//注册路由
export default new Router({
    // mode:'history',
    routes: [
        {
            path: '/', //路径
            name: 'Login', //渲染位置
            component: Login,  //引用名
            meta: { title:'智能化管理系统', keyword:'', description:'', },
        },
        {
            path: '/index', //路径
            name: 'Index', //渲染位置
            component: Index,  //引用名
            meta: { title:'首页', keyword:'', description:'', },
            redirect:'/index/welcom',
            children:[
                {
                    path: '/index/welcom', //路径
                    name: 'Welcome', //渲染位置
                    component: Welcome2,  //引用名
                    meta: { title:'首页', keyword:'', description:'', },
                },
                {
                    path: '/index/message', //路径
                    name: 'message', //渲染位置
                    component: Message,  //引用名
                    meta: { title:'消息', keyword:'', description:'', },
                },
                {
                    path: '/index/warning', //路径
                    name: 'warning', //渲染位置
                    component: Warning,  //引用名
                    meta: { title:'告警策略', keyword:'', description:'', },
                },
                {
                    path: '/index/warninglog', //路径
                    name: 'warninglog', //渲染位置
                    component: Warninglog,  //引用名
                    meta: { title:'告警日志', keyword:'', description:'', },
                },
                {
                    path: '/index/devices', //路径
                    name: 'devices', //渲染位置
                    component: Devices,  //引用名
                    meta: { title:'设备管理', keyword:'', description:'', },
                },
                {
                    path: '/index/oid', //路径
                    name: 'oid', //渲染位置
                    component: OID,  //引用名
                    meta: { title:'OID管理', keyword:'', description:'', },
                },
                {
                    path: '/index/thirdparty', //路径
                    name: 'thirdparty', //渲染位置
                    component: Thirdparty,  //引用名
                    meta: { title:'服务列表', keyword:'', description:'', },
                },
                {
                    path: '/index/interface', //路径
                    name: 'interface', //渲染位置
                    component: InterFace,  //引用名
                    meta: { title:'接口设置', keyword:'', description:'', },
                },
                {
                    path: '/index/interfaceview', //路径
                    name: 'interfaceview', //渲染位置
                    component: InterFaceView,  //引用名
                    meta: { title:'接口状态', keyword:'', description:'', },
                },
                {
                    path: '/index/services', //路径
                    name: 'services', //渲染位置
                    component: Services,  //引用名
                    meta: { title:'设备列表', keyword:'', description:'', },
                },
                {
                    path: '/index/services/servicesinfo', //路径
                    name: 'servicesinfo', //渲染位置
                    component: Servicesinfo,  //引用名
                    meta: { title:'设备信息', keyword:'', description:'', },
                },
                {
                    path: '/index/warninfo', //路径
                    name: 'warninfo', //渲染位置
                    component: Warninfo,  //引用名
                    meta: { title:'设备信息', keyword:'', description:'', },
                    redirect:'/index/warninfo/wappush',
                    children:[
                        {
                            path: '/index/warninfo/wappush', //路径
                            name: 'wappush', //渲染位置
                            component: Wappush,  //引用名
                            meta: { title:'服务信息', keyword:'', description:'', },
                        },
                        {
                            path: '/index/warninfo/cpuuse', //路径
                            name: 'cpuuse', //渲染位置
                            component: Cpuuse,  //引用名
                            meta: { title:'CPU使用率', keyword:'', description:'', },
                        },
                        {
                            path: '/index/warninfo/memoryuse', //路径
                            name: 'memoryuse', //渲染位置
                            component: Memoryuse,  //引用名
                            meta: { title:'内存使用率', keyword:'', description:'', },
                        },
                        {
                            path: '/index/warninfo/connectnumber', //路径
                            name: 'connectnumber', //渲染位置
                            component: Connectnumber,  //引用名
                            meta: { title:'连接数', keyword:'', description:'', },
                        },
                        {
                            path: '/index/warninfo/servicelog', //路径
                            name: 'servicelog', //渲染位置
                            component: Servicelog,  //引用名
                            meta: { title:'服务日志', keyword:'', description:'', },
                        },
                        {
                            path: '/index/warninfo/serviceconfig', //路径
                            name: 'servicelog', //渲染位置
                            component: serviceConfig,  //引用名
                            meta: { title:'服务配置', keyword:'', description:'', },
                        },
                        {
                            path: '/index/warninfo/connectfail', //路径
                            name: 'connectfail', //渲染位置
                            component: Connectfail,  //引用名
                            meta: { title:'基本信息', keyword:'', description:'', },
                        },
                    ]
                },
                {
                    path: '/index/iptableInfo', //路径
                    name: 'iptableInfo', //渲染位置
                    component: IPtableInfo,  //引用名
                    meta: { title:'设备信息', keyword:'', description:'', },
                    redirect:'/index/iptableInfo/ip',
                    children:[
                        {
                            path: '/index/iptableInfo/ip', //路径
                            name: 'ip', //渲染位置
                            component: IP,  //引用名
                            meta: { title:'端口规则', keyword:'', description:'', },
                        },
                        {
                            path: '/index/iptableInfo/iprule', //路径
                            name: 'iprule', //渲染位置
                            component: IPrule,  //引用名
                            meta: { title:'IP规则', keyword:'', description:'', },
                        },
                    ]
                },
                {
                    path: '/index/esinfo', //路径
                    name: 'esinfo', //渲染位置
                    component: EsInfo,  //引用名
                    meta: { title:'设备信息', keyword:'', description:'', },
                    redirect:'/index/esinfo/es',
                    children:[
                        {
                            path: '/index/esinfo/es', //路径
                            name: 'es', //渲染位置
                            component: ES,  //引用名
                            meta: { title:'ES服务', keyword:'', description:'', },
                        },
                    ]
                },
                {
                    path: '/index/custom', //路径
                    name: 'custom', //渲染位置
                    component: Custom,  //引用名
                    meta: { title:'人员管理', keyword:'', description:'', },
                },
                {
                    path: '/index/word', //路径
                    name: 'Word', //渲染位置
                    component: Word,  //引用名
                    meta: { title:'字冠设置', keyword:'', description:'', },
                },
                {
                    path: '/index/params', //路径
                    name: 'params', //渲染位置
                    component: Params,  //引用名
                    meta: { title:'系统参数', keyword:'', description:'', },
                },
                {
                    path: '/index/allowance', //路径
                    name: 'allowance', //渲染位置
                    component: Allowance,  //引用名
                    meta: { title:'余量', keyword:'', description:'', },
                },
            ]
        },
    ],
})



