 * @Author: GaoX
 * @Date: 2022-04-24 14:33:02
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-05-24 17:47:49
-->
<template>
  <el-table
    :data="tableData"
    style="width: 100%"
    :border="true">
    <el-table-column
      prop="nodename"
      width="150">
      <template slot-scope="scope">
        <span>unassigned</span>
        <span>
          {{ scope.row.nodename }}
        </span>
      </template>
    </el-table-column>
    <el-table-column
      v-for="(index,name) in list"
      :key="name"
      :label="name"
      width="300"
      >
    </el-table-column>
  </el-table>
</template>

<script>
export default {
  props:{
      hostId:{
          default:'',
      },
      roleId:{
          default:'',
      }
  },
  data() {
    return {
      tableData: [],
      list:[],
      nodename:"",
    }
  },
  methods: {
    elasticSearchInfo(){
        var params = {
            hostId:this.hostId,
            roleId:this.roleId
        }
        capis.elasticSearchInfo(params,{})
        .then(data=>{
            this.tableLoading = false;
            if(data.status == 'success'){
                //对数据进行赋值
                this.tableData = data.res.data;
                this.list = this.tableData.es_health_info.recovery;
                this.nodename = this.tableData.es_health_info.recovery.sha;
                // console.log("nodename",this.nodename)
                console.log('55',this.list)
                console.log("pr",this.tableData)
            }else{
                this.$notify({
                    title: '提示',
                    message: data.des,
                    type: 'warning'
                });
            }
        
        })
        .catch(err=>{
            this.tableLoading = false;
            this.$notify({
                title: '提示',
                message: '操作失败',
                type: 'warning'
            });
        })
    },
  },
  created(){
      this.elasticSearchInfo();
  },
}
</script>

<style scoped lang="scss">
    
    .serviceinfo-content{
        width: 50%;
        .serviceinfo-title{
            display: flex;
            justify-content: space-between;
            font-size: 20px;
            font-weight: 600;
            margin-bottom: 20px;
            .left{
                display: flex;
            }
            .serviceinfo-border{
                width: 10px;
                height: 26px;
                background-color: #5e5ec6;
                border-radius: 5px;
                margin-left: 20px;
            }
            .serviceinfo-tit{
                margin-left: 18px;
            }
        } 
    }
       
</style>


