 * @Descripttion: 设备管理
 * @version: 1.0
 * @Author: GaoX
 * @Date: 2022-04-11 22:42:04
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-05-26 10:12:48
-->
<template>
    <div class="devices">
        <div class="message-top" ref="message-top">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline" @keydown.enter.native="WebViewinterfaceStatusList('search')">
                <el-form-item label="接口名称：">
                    <el-input v-model="searchForm.title" placeholder="请输入" clearable></el-input>
                </el-form-item>
                <!-- <el-form-item label="URL：">
                    <el-input v-model="searchForm.url" placeholder="请输入" clearable></el-input>
                </el-form-item>
                <el-form-item label="协议类型：">
                    <el-select v-model="searchForm.status" placeholder="请选择" clearable>
                        <el-option label="Http" value="http"></el-option>
                        <el-option label="Https" value="https"></el-option>
                    </el-select>
                </el-form-item> -->
                <el-form-item>
                    <el-button type="primary" icon="el-icon-search" @click="WebViewinterfaceStatusList('search')" :loading="tableLoading">查询</el-button>
                    <!-- <el-button icon="el-icon-circle-plus-outline" type="success" @click="serviceShow = !serviceShow">新增</el-button> -->
                </el-form-item>
            </el-form>
            
        </div>
        <div class="message-table">
            <el-table
                ref="record-table"
                :data="list"
                style="width: 100%"
                stripe
                :height = "tableHeight"
                highlight-current-row
                v-loading="tableLoading"
            >
                <el-table-column
                    type="index"
                    width="50">
                </el-table-column>
                <el-table-column
                label="接口名称"
                 prop="title"
                sortable
                show-overflow-tooltip
                >
                </el-table-column>
                <el-table-column
                label="URL"
                 prop="url"
                sortable
                show-overflow-tooltip
                >
                </el-table-column>
                <el-table-column
                label="协议类型"
                prop="type"
                sortable
                show-overflow-tooltip
                >
                </el-table-column>
                <el-table-column
                label="超时时长"
                prop="timeout"
                sortable
                show-overflow-tooltip
                >
                <template slot-scope="scope">
                        {{scope.row.timeout}} s
                </template>
                </el-table-column>
                <el-table-column
                label="响应时长"
                prop="response_time"
                sortable
                show-overflow-tooltip
                >
                <template slot-scope="scope">
                        {{scope.row.response_time}} s
                </template>
                </el-table-column>
                <el-table-column
                label="错误描述"
                prop="status_msg"
                sortable
                show-overflow-tooltip
                >
                </el-table-column>
                <el-table-column
                label="更新时间"
                prop="updated_at"
                sortable
                fixed="right"
                show-overflow-tooltip
                >
                </el-table-column>
                <el-table-column
                label="状态"
                >
                    <template slot-scope="scope">
                        <div class="dedevice-run">
                            <span  v-if="scope.row.running == '1'"><i class="el-icon-circle-check" style="color:#67C23A"></i> 正常</span>
                            <span v-else><i class="el-icon-circle-close" style="color:#F56C6C"></i> 异常</span>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column
                label="最后采集时间"
                prop="gathering_time"
                sortable
                show-overflow-tooltip
                >
                </el-table-column>
            </el-table>
        </div>
        <div class="message-pagenation" ref="message-pagenation">
            <el-pagination
                :page-sizes="[10, 20, 50, 100]"
                :page-size="pagenation.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pagenation.total"
                :current-page="pagenation.page"
                @size-change='sizeChange'
                @current-change='curreantChange'
                >
            </el-pagination>
        </div>
        <el-dialog width="50%" :visible.sync="noticeShow" :lock-scroll="true" :center="true" :show-close="false" custom-class="service_dialog" :destroy-on-close="true">
            <Notice v-on:freshData="freshData" v-if="noticeShow" :hostId.sync='hostId'></Notice>
        </el-dialog>
    </div>
</template>
<script>
    import Notice from './add/notice.vue'
    export default{
        data(){
            return{
                list:[],//表格数据
                tableHeight:600, // 表格高度
                serviceShow:false,
                serviceEditShow:false,
                noticeShow:false, //策略规则 的显示与不显示
                editId:'',
                hostId:'',
                pagenation:{
                    page:1,
                    pagesize:10,
                    total:0
                },
                tableLoading:false, //表格的加载中
                editId:'', //修改的id
                searchForm:{
                    title:'',
                    url:''
                }
            }
        },
        methods:{
            getHeight(){
                this.tableHeight = window.innerHeight-this.$refs['record-table'].$el.offsetTop-50;
            },
            WebViewinterfaceStatusList(type){
                if(type=='search')
                {
                    //是查询
                    this.pagenation.page=1;
                }
                //获取到设备列表
                this.tableLoading = true; //加载中
                var params = {
                    page:this.pagenation.page,
                    pagesize:this.pagenation.pagesize,
                }
                params = {...params,...this.searchForm};
                capis.WebViewinterfaceStatusList(params,{})
                .then(data=>{
                    this.tableLoading = false;
                    if(data.status == 'success'){
                        //对数据进行赋值
                        this.list = data.res.data;
                        //对总数进行赋值
                        this.pagenation.total=data.res.total;
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
            },
            freshData(data)
            {
                // 子组件给父组件传的至
                if(data && data.type == 'cancel')
                {
                    // 子组件取消操作,隐藏弹出层
                    
                }
                else if(data && data.type == 'update')
                {
                    //子组件点击了确认操作， 编辑的更新操作
                    this.WebViewinterfaceStatusList();
                }
                else if(data && data.type == 'add')
                {
                    //子组件点击了确认操作， 新增
                    //把页面赋值为1，然后进行更新操作
                    this.pagenation.page = 1;
                    this.WebViewinterfaceStatusList();
                }
                this.serviceShow = false;
                this.serviceEditShow = false;
            },
            changeStatus(data)
            {
                 this.formLoading=true;
                    //请求接口
                     var params = {
                        act:'changeStatus',
                        id:data.row.id,
                        status:data.row.status
                     }
                    capis.WebViewinterfaceSave(params,{})
                    .then(data=>{
                        if(data.status == 'success'){
                            this.$notify({
                                title: '提示',
                                message: '操作成功',
                                type: 'success'
                            });
                        }else{
                            this.$notify({
                                title: '提示',
                                message: data.des,
                                type: 'warning'
                            });
                        }
                    
                    })
                    .catch(err=>{
                        this.$notify({
                            title: '提示',
                            message: '操作失败',
                            type: 'warning'
                        });
                    })
            },
            sizeChange(pagesize){
                //更改每页的显示条数
                this.pagenation.pagesize = pagesize;
                this.WebViewinterfaceStatusList();
            },
            curreantChange(page)
            {
                this.pagenation.page = page;
                this.WebViewinterfaceStatusList();
            },
            noticeInfo(data)
            {
                this.hostId = data.row.id;
                this.noticeShow = true ;
            },
            initWs(){
                //检测主机状态
                this.$LaravelEcho.instance.channel('HostStatus').listen('Snmp.HostEvent', (e) => {
                    if(e.type=="status")
                    {
                        if(this.list.length>0)
                        {
                            this.list.forEach((value,index) => {
                                if(value.id==e.host_id)
                                {
                                    this.list[index].running = e.data.running;
                                }
                            });
                        }
                    }
                })
            }
        },
        created(){
            //获取到服务器列表
            this.WebViewinterfaceStatusList();
            this.$nextTick(()=>{
                this.getHeight();
                this.initWs();
                window.onresize = () => {
                    this.getHeight();
                };
            });
        },
        components:{
            Notice
        },
        computed:{
            unsigns () {
                return this.table.filter(item => item.status === '1')
            },
        },
        beforeDestroy(){
            console.log("退出监听通道:HostStatus");
            this.$LaravelEcho.instance.leaveChannel('HostStatus');
        },
            
    }
</script>

<style scoped lang="scss">
    .devices{
        box-sizing: border-box;
        background-color: #fff;
        box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
        border-radius: 20px;
        padding: 40px 40px;
        height: 100%;
        .message-top{
            .message-title{
                font-size: 24px;
                font-weight: 900;
            }
            .message-type{
                box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
            }
        }
        .message-pagenation{
            text-align: center;
            padding: 10px 0;
        }
        .dedevice-run{
            display: flex;
            align-items: center;
        }
    }
</style>
<style>
.service_dialog{
       border-radius: 15px;
       box-shadow: 0 0 30px 0 rgba(94, 94, 198, 0.7);
       box-sizing: border-box;
   }
</style>



