<!--
 * @Descripttion: 服务日志
 * @version: 1.0
 * @Author: GaoX
 * @Date: 2022-04-21 12:18:09
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-04-24 16:59:31
-->
<template>
    <div>
        <div>
            <el-row class="tac">
                <el-col class="tac-item">
                    <textarea ref="log-info" v-model="fileInfo" readonly style="width: 100%;height: 65vh;background-color: #333333;color:#fff;border: none;" @scroll="scrollEvent('log-info')"></textarea>
                </el-col>
            </el-row>
        </div>
    </div>
</template>

<script>
    export default {
        props:{
            fileName:{
                default:''
            },
            hostId:{
                default:''
            },
            type:{
                default:''
            },
        },
        data(){
            return{
                fileInfo:'',
                startLine:1,
                totalLine:0
            }
        },
        methods: {
            ClickLog(startLine){
                var params = {
                    server:this.type,
                    fileName:this.fileName,
                    start:startLine
                }
                capis.hostseverLog(this.hostId,params,{})
                .then(data=>{
                    if(data.status == 'success'){
                        //对数据进行赋值
                        this.fileInfo = this.fileInfo + data.res.data.fileContent;
                        this.startLine = data.res.data.endLine;
                        this.totalLine = data.res.data.totalLine;
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                })
                .catch(err=>{
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
            },
            scrollEvent(dom) {
                if(this.$refs[dom].scrollHeight - (this.$refs[dom].scrollTop + this.$refs[dom].offsetHeight) == 0)
                {
                    this.ClickLog(this.startLine)
                }
            }
        },
        created(){
            this.ClickLog(1);
        },
        components:{
        },
    }
</script>

<style scoped lang="scss">
.tac{
    // text-align:center; /* 文字居中 */
    .tac-item{
        margin-top: -30px;
        margin-left: -5px;
    }
}
</style>


