 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-05-26 09:52:46
-->
<template>
    <div class="warning-add">
        <p class="warning-add-title">基础选项</p>
        <el-form ref="form" :model="form" :rules="rules" label-width="100px" v-loading="formLoading">
             
             <el-form-item label="接口名称：" prop="title">
                 <el-input v-model="form.title" clearable></el-input>
             </el-form-item>
             <el-form-item label="URL：" prop="url">
                 <el-input v-model="form.url" clearable></el-input>
             </el-form-item>
             <el-form-item label="接口端口：" prop="port">
                 <el-input v-model="form.port" clearable></el-input>
             </el-form-item>
             <el-form-item label="协议类型：" prop="type">
                 <el-select v-model="form.type" placeholder="请选择">
                    <el-option
                        label="Http"
                        value="http">
                    </el-option>
                    <el-option
                        label="Https"
                        value="https">
                    </el-option>
                    <el-option
                        label="Mysql"
                        value="mysql">
                    </el-option>
                 </el-select>
             </el-form-item>
             <div v-if="form.type =='mysql'">
                <el-form-item label="账　　号：" prop="db_username">
                    <el-input v-model="form.db_username" clearable></el-input>
                </el-form-item>
                <el-form-item label="密　　码：" prop="db_password">
                    <el-input v-model="form.db_password" clearable type='password' show-password></el-input>
                </el-form-item>
            </div>
             <el-form-item label="超时时长：">
                <el-form-item prop="cycle">
                    <el-input v-model="form.timeout" clearable placeholder="超时时长" type="number"><template slot="append">秒</template></el-input>
                </el-form-item>
            </el-form-item>
            <!-- <el-form-item label="响应时长：">
                <el-form-item prop="cycle">
                    <el-input v-model="form.response_time" clearable placeholder="超时时长" type="number"><template slot="append">秒</template></el-input>
                </el-form-item>
            </el-form-item> -->
             <el-form-item label="备注：" prop="remark">
                 <el-input v-model="form.remark" clearable></el-input>
             </el-form-item>
             <el-form-item label="监控状态：" prop="status">
                  <el-switch
                     v-model="form.status"
                     active-color="#13ce66"
                     inactive-color="#ff4949"
                     :active-value='1'
                     :inactive-value='2'
                     >
                 </el-switch>
             </el-form-item>
             <el-form-item>
                 <el-button type="primary" @click="submitForm('form')" :loading="formLoading=false">编辑</el-button>
                 <el-button @click="cancel">取消</el-button>
             </el-form-item>
         </el-form>
    </div>
 </template>
 <script>
    export default{
        props:{
            editId:{
                default:''
            }
        },
        data(){
            return{
                form:{
                    type:[], //服务类型
                    title:'',
                    url:'',
                    port:'',
                    db_username:'',
                    db_password:'',
                    timeout:'',
                    // response_time:'',
                    remark:'',
                    type:'',
                    status:1,
                },
                rules: {
                    title: [
                        { required: true, message: '请输入名称', trigger: 'blur' },
                    ],
                    url: [
                        { required: true, message: '请输入地址', trigger: 'blur' },
                    ],
                    port: [
                        { required: true, message: '请输入地址', trigger: 'blur' },
                    ],
                    type: [
                        { required: true, message: '请选择类型', trigger: 'blur' },
                    ],
                    db_username: [
                        { required: true, message: '请输入账号', trigger: 'blur' },
                    ],
                    db_password: [
                        { required: true, message: '请输入密码', trigger: 'blur' },
                    ],
                },
                formLoading:false,
            }
        },
        components:{
        },
        created(){
            this.getNotificationSettingListEnable();//告警规则
            this.WebViewinterfaceDetail();//接口详情
            this.getNotificationSettingListEnable();
        },
        methods: {
            submitForm(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.formLoading=true;
                        //请求接口
                        var params = {
                            id:this.editId,
                            title:this.form.title,
                            url:this.form.url,
                            port:this.form.port,
                            type:this.form.type,
                            status:this.form.status,
                            remark:this.form.remark,
                            db_username:this.form.db_username,
                            db_password:this.form.db_password,
                            // response_time:this.form.response_time,
                            timeout:this.form.timeout
                        }
                        capis.WebViewinterfaceSave(params,{})
                        .then(data=>{
                            console.log('保存的数据',params)
                            this.formLoading = false;
                            if(data.status == 'success'){
                                //对数据进行赋值
                                // 修改父组件的值
                                this.$emit('freshData',{
                                    type:'update',
                                })
                            }else{
                                this.$notify({
                                    title: '提示',
                                    message: data.des,
                                    type: 'warning'
                                });
                            }
                        
                        })
                        .catch(err=>{
                            this.formLoading = false;
                            this.$notify({
                                title: '提示',
                                message: '操作失败',
                                type: 'warning'
                                });
                            })
                        
                        }
                    });
            },
            cancel(){
                this.$emit('freshData',{
                    type:'cancel',
                })
            },
                //接口详情
                WebViewinterfaceDetail(){
                    var params = {
                        id:this.editId
                    };
                    capis.WebViewinterfaceDetail(params,{})
                    .then(data=>{
                        if(data.status == 'success'){
                            //对数据进行赋值
                            this.form.title = data.res.title;
                            this.form.url = data.res.url;
                            this.form.port = data.res.port;
                            this.form.remark = data.res.remark;
                            this.form.type = data.res.type;
                            this.form.status = data.res.status;
                            this.form.timeout = data.res.timeout;
                            this.form.db_username = data.res.db_username;
                            this.form.db_password = data.res.db_password;
                            // this.form.response_time = data.res.response_time;
                        }else{
                            this.$notify({
                                title: '提示',
                                message: data.des,
                                type: 'warning'
                            });
                        }
                    })
                    .catch(err=>{
                        this.$notify({
                            title: '提示',
                            message: '操作失败',
                            type: 'warning'
                        });
                    })
                },
                getNotificationSettingListEnable(){
                var params = {};
                capis.getNotificationSettingListEnable(params,{})
                .then(data=>{
                    this.tableLoading = false;
                    if(data.status == 'success'){
                        //对数据进行赋值
                        this.NotificationSettingIdArr = data.res.data;
                        this.NotificationSettingIdArr.forEach((value,index) => {
                            this.NotificationSettingIdArr[index].id = value.id.toString();
                        });
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
            },
            }
        }
 </script>
 <style scoped lang="scss">
    .warning-add{
        padding: 0 80px;
        .warning-add-title{
            font-size: 28px;
            font-weight: 600;
            height: 60px;
            line-height: 60px;
        }
        .el-select{
            display: block;
        }
    }
 </style>


