            </div>
            <div class="item">
                <p class="item-title"><i class="el-icon-data-line"></i><span>运行状态 </span></p>
                <p class="item-name">{{serveInfo.status?'运行中':'停止'}}</p>
            </div>
            <div class="item">
                <p class="item-title"><i class="el-icon-cpu"></i><span>cpu使用率</span></p>
                <p class="item-name">{{serveInfo.cpu_use}} %</p>
            </div>
            <div class="item">
                <p class="item-title"><i class="el-icon-bank-card"></i><span>内存使用率</span></p>
                <p class="item-name">{{serveInfo.memory_use}} %</p>
            </div>
            <div class="item">
                <p class="item-title"><i class="el-icon-tickets"></i><span>运行时间</span></p>
                <p class="item-name">{{serveInfo.runtime}}</p>
            </div>
            <div class="item">
                <p class="item-title"><i class="el-icon-tickets"></i><span>连接数</span></p>
                <p class="item-name">{{serveInfo.connect}}</p>
            </div>
        </div>
        <div class="content-box">
            <CPUuse :roleId="roleId" :hostId="hostId"></CPUuse>
            <MemoryUse :roleId="roleId" :hostId="hostId"></MemoryUse>
            <ConnectNumber :roleId="roleId" :hostId="hostId"></ConnectNumber>
        </div>
    </div>
</template>

