import Vue from 'vue'
import axios from "axios";
import qs from "qs";
import { setTimeout } from 'timers'

const pyConfig = require('./pyconfig');
var baseUrl = pyConfig.ROOT;

let header = {
	  'X-Requested-With': 'XMLHttpRequest',
    "Content-Type": "application/x-www-form-urlencoded; charset=utf-8"
};

const httpService = axios.create({
  baseURL: baseUrl,
  timeout: 600000,
  responseType: "json",
  transformRequest: [data => qs.stringify(data)],
  headers: header

});

/*是否有请求正在刷新token*/
window.isRefreshing = false
// 挂起的请求数组
let refreshSubscribers = []

/*push所有请求到数组中*/
function subscribeTokenRefresh (cb) {
  refreshSubscribers.push(cb)
}

/*刷新请求（refreshSubscribers数组中的请求得到新的token之后会自执行，用新的token去请求数据）*/
function onRrefreshed (token) {
  refreshSubscribers.map(cb => cb(token))
}

function checkHeader(headers) {
  if(headers.hasOwnProperty("authorization"))
  {
    let access_token = headers.authorization;
    let expires_in = headers.expires;
    access_token = access_token.replace("jinGuEoms/","")
    window.isRefreshing = false
    window.localStorage.setItem('access_token',access_token);
    window.localStorage.setItem('expires_in',parseInt(expires_in));
    window.localStorage.setItem('token_time',new Date().getTime());
    return true;
  }
  return false;
}

httpService.interceptors.request.use(
  config => {
    return config;
  },
  error => Promise.reject(error)
);
httpService.interceptors.request.use(
  config => {
    let accessToken = window.localStorage.getItem('access_token');
    if (accessToken && accessToken !== '') {
      config.headers.common['Authorization'] = window.localStorage.getItem('token_type') + " " + accessToken;
    }
    // 判断是否需要更新token
    let token_time = window.localStorage.getItem('token_time')
    let expires_in = window.localStorage.getItem('expires_in')
	  let now = new Date().getTime();
    if (now >= (parseInt(expires_in)*1000+parseInt(token_time)) && (config.url.indexOf("/api/auth/login") === -1 && config.url.indexOf("/api/web/setting/list/enable") === -1 && config.url.indexOf("/api/auth/refresh") === -1 && config.url.indexOf("/api/auth/logout"))) {
		if (!window.isRefreshing) {
			/*将刷新token的标志置为true*/
			window.isRefreshing = true
			post('/api/auth/refresh').then(res => {
        config.headers.common['Authorization'] = window.localStorage.getItem('token_type') + " " + accessToken;
				/*将标志置为false*/
				  window.isRefreshing = false
					window.localStorage.setItem('access_token',res.res.original.access_token);
					window.localStorage.setItem('expires_in',res.res.original.expires_in);
					window.localStorage.setItem('token_type',res.res.original.token_type);
					window.localStorage.setItem('token_time',new Date().getTime());
				/*执行数组里的函数,重新发起被挂起的请求*/
				onRrefreshed(res.res.original.token_type + " " +res.res.original.access_token)
				/*执行onRefreshed函数后清空数组中保存的请求*/
				refreshSubscribers = []
			}).catch(err => {
        if(checkHeader(err.response.headers))
        {
          let access_token = err.response.headers.authorization;
          access_token = access_token.replace("jinGuEoms/","")
          /*执行数组里的函数,重新发起被挂起的请求*/
          onRrefreshed(window.localStorage.getItem('token_type') + " " +access_token)
          /*执行onRefreshed函数后清空数组中保存的请求*/
          refreshSubscribers = []
        }
        else
        {
          ctool.gotLogin({
              title: '请重新登录',
              message: '当前登录已失效，请重新登录',
              type: 'warning'
          });
        }
			})
		  }
		  let retry = new Promise((resolve, reject) => {
			/*(token) => {...}这个函数就是回调函数*/
			subscribeTokenRefresh((token) => {
        config.headers.common['Authorization'] = token;
			  /*将请求挂起*/
			  resolve(config)
			})
		})
		return retry
    }
    return config;
  },
  error => Promise.reject(error)
);

httpService.interceptors.response.use(
  response => {
    checkHeader(response.headers)
	    if (response.status == 401) {
        ctool.gotLogin({
          title: '请重新登录',
          message: '当前登录已失效，请重新登录',
          type: 'warning'
      });
		}
    // 统一处理状态
    return response.data;
  },
  // 处理处理
  error => {
    if (error && error.response) {
      if(!checkHeader(error.response.headers))
      {
        switch (error.response.status) {
          case 419:
            ctool.gotLogin({
              title: '请重新登录',
              message: '当前登录已失效，请重新登录',
              type: 'warning'
          });
            break;
          case 401:
            ctool.gotLogin({
              title: '请重新登录',
              message: '当前登录已失效，请重新登录',
              type: 'warning'
          });
              break;
        }
      }
    }
    return Promise.reject(error);
  }
);



/*网络请求部分*/

/*
 *  get请求
 *  url:请求地址
 *  params:参数
 * */

export function get (url, params = {}, headers) {
  return new Promise((resolve, reject) => {
    httpService({
      url: url,
      method: `get`,
      params: params
    })
      .then(response => {
        resolve(response);
      })
      .catch(error => {
        reject(error);
      });
  });
}

/*
 *  post请求
 *  url:请求地址
 *  params:参数
 * */
export function post (url, params = {}, headers) {
  return new Promise((resolve, reject) => {
    httpService({
      url: url,
      method: `post`,
      data: params,
    })
      .then(response => {
        resolve(response);
      })
      .catch(error => {
        reject(error);
      });
  });
}

/*
 *  文件上传
 *  url:请求地址
 *  params:参数
 * */
export function fileUpload (url, params = {}) {
  return new Promise((resolve, reject) => {
    httpService({
      url: url,
      method: `post`,
      data: params,
      headers: { "Content-Type": `multipart/form-data` }
    })
      .then(response => {
        resolve(response);
      })
      .catch(error => {
        reject(error);
      });
  });
}

export default {
  get,
  post,
  fileUpload
};


