 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-05-26 09:52:46
-->
<template>
    <div class="warning-add">
        <p class="warning-add-title">基础选项</p>
        <el-form ref="form" :model="form" :rules="rules" label-width="100px" v-loading="formLoading">
             <el-form-item label="名　　称：" prop="name">
                 <el-input v-model="form.name" clearable></el-input>
             </el-form-item>
             <el-form-item label="标　　识：" prop="key">
                 <el-input v-model="form.key" clearable></el-input>
             </el-form-item>
             <el-form-item label="端　　口：" prop="port">
                 <el-input v-model="form.port" clearable></el-input>
             </el-form-item>
             <el-form-item label="授权用户：" prop="user">
                 <el-input v-model="form.user" clearable></el-input>
             </el-form-item>
             <el-form-item label="授权密码：" prop="password">
                 <el-input v-model="form.password" show-password clearable></el-input>
             </el-form-item>
             <el-form-item>
                 <el-button type="primary" @click="submitForm('form')" :loading="formLoading">确定</el-button>
                 <el-button @click="cancel">取消</el-button>
             </el-form-item>
         </el-form>
    </div>
 </template>
 <script>
    export default{
        props:{
            id:{
                default:''
            }
        },
        data(){
            return{
                form:{
                    name:'',
                    key:'',
                    port:'',
                    user:'',
                    password:'',
                },
                rules: {
                    name: [
                        { required: true, message: '请输入名称', trigger: 'blur' },
                    ],
                    key: [
                        { required: true, message: '请输入标识', trigger: 'blur' },
                    ],
                    port: [
                        { required: true, message: '请输入端口', trigger: 'blur' },
                    ],
                },
                formLoading:false,
            }
        },
        components:{
        },
        created(){
            this.getRoleIndexList();
            this.RoleIndexListInfo();
        },
        methods: {
            submitForm(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.formLoading=true;
                        //请求接口
                        var params = {
                            password:this.form.password,
                            user:this.form.user,
                            port:this.form.port,
                            name:this.form.name,
                            key:this.form.key,
                            id:this.id,
                        }
                        capis.saveRoleIndexList(params,{})
                        .then(data=>{
                            this.formLoading = false;
                            if(data.status == 'success'){
                                //对数据进行赋值
                                // 修改父组件的值
                                this.$emit('freshData',{
                                    type:'update',
                                })
                            }else{
                                this.$notify({
                                    title: '提示',
                                    message: data.des,
                                    type: 'warning'
                                });
                            }
                        
                        })
                        .catch(err=>{
                            this.formLoading = false;
                            this.$notify({
                                title: '提示',
                                message: '操作失败',
                                type: 'warning'
                            });
                        })
                     
                    }
                });
            },
            getRoleIndexList(){
                var params = {};
                capis.getRoleIndexList(params,{})
                .then(data=>{
                    this.tableLoading = false;
                    if(data.status == 'success'){
                        //对数据进行赋值
                        this.roleList = data.res;
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
            },
            RoleIndexListInfo(){
                var params = {
                    id:this.id
                };
                capis.RoleIndexListInfo(params,{})
                .then(data=>{
                    if(data.status == 'success'){
                        //对数据进行赋值
                        this.form.name = data.res.data.name;
                        this.form.key = data.res.data.key;
                        this.form.port = data.res.data.port;
                        this.form.user = data.res.data.user;
                        this.form.password = data.res.data.password;
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
            },
            cancel(){
                this.$emit('freshData',{
                    type:'cancel',
                })
            }, 
        }
    }
 </script>
 <style scoped lang="scss">
    .warning-add{
        padding: 0 80px;
        .warning-add-title{
            font-size: 28px;
            font-weight: 600;
            height: 60px;
            line-height: 60px;
        }
        .el-select{
            display: block;
        }
    }
 </style>


