 * @Descripttion: 服务日志
 * @version: 1.0
 * @Author: GaoX
 * @Date: 2022-04-21 12:18:09
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-04-24 16:59:31
-->
<template>
    <div>
        <div>
            <el-row class="tac">
                <el-col class="tac-item">
                    <textarea ref="log-info" v-model="form.fileInfo"  style="width: 100%;height: 65vh;background-color: #333333;color:#fff;border: none;margin-bottom: 10px;"></textarea>
                    <el-button type="primary" @click="hostseverSetconf">保存</el-button>
                </el-col>
            </el-row>
        </div>
    </div>
</template>

<script>
    export default {
        props:{
            fileName:{
                default:''
            },
            hostId:{
                default:''
            },
            type:{
                default:''
            },
        },
        data(){
            return{
                form:{
                    fileInfo:'',
                }
            }
        },
        methods: {
            ClickLog(){
                var params = {
                    server:this.type,
                    fileName:this.fileName,
                    // start:startLine
                }
                capis.hostseverConf(this.hostId,params,{})
                .then(data=>{
                    if(data.status == 'success'){
                        //对数据进行赋值
                        this.form.fileInfo = data.res.data
                    }
                    else
                    {
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                })
                .catch(err=>{
                    this.$notify({
                        title: '提示',
                        message: err,
                        type: 'warning'
                    });
                })
            },
            scrollEvent(dom) {
                if(this.$refs[dom].scrollHeight - (this.$refs[dom].scrollTop + this.$refs[dom].offsetHeight) == 0)
                {
                    this.ClickLog()
                }
            },
            hostseverSetconf(){
                var params = {
                    server:this.type,
                    fileName:this.fileName,
                    data:this.form.fileInfo,
                }
                console.log(11,params)
                capis.hostseverSetconf(this.hostId,params,{})
                .then(data=>{
                    if(data.status == 'success')
                    {
                        this.$emit('freshData',{
                            type:'update',
                        })
                    }
                    else
                    {
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                })
            }
        },
        created(){
            this.ClickLog(1);
        },
        components:{
        },
    }
</script>

<style scoped lang="scss">
    .tac{
        // text-align:center; /* 文字居中 */
        .tac-item{
            margin-top: -30px;
            margin-left: -5px;
        }
    }
    // .readonly .textarea .el-textarea__inner {
    //     background-color: #333 !important;
    // }
</style>


