<!--
 * @Descripttion: 服务配置
 * @version: 1.0
 * @Author: GaoX
 * @Date: 2022-04-21 12:18:09
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-04-24 16:59:31
-->
<template>
    <div>
        <el-table
        ref="record-table"
        :data="list"
        style="width: 100%"
        stripe
        :height = "tableHeight"
        highlight-current-row
        v-loading="tableLoading">
            <el-table-column
            prop="fileName"
            label="文件名称"
            width="300">
            <template slot-scope="scope">
                            <el-link type="primary" @click="LogInfo(scope);">{{scope.row.fileName}}</el-link>
                        </template>
            </el-table-column>
            <el-table-column
            prop="fileSize"
            label="文件大小">
            </el-table-column>
            <el-table-column
            prop="group"
            label="所属分组">
            </el-table-column>
            <el-table-column
            prop="owner"
            label="所属用户">
            </el-table-column>
            <el-table-column
            prop="mode"
            label="权限">
            </el-table-column>
            <el-table-column
            prop="lastModificationTime"
            label="最后一次修改时间">
            </el-table-column>
        </el-table>
        <el-dialog width="60%" :visible.sync="LogShow" :lock-scroll="true" :center="true" :show-close="false" custom-class="service_dialog" :destroy-on-close="true">
            <Condir v-if="LogShow" :hostId.sync='hostId' :type.sync="type" :fileName.sync="fileName"></Condir>
        </el-dialog>
    </div>  
</template>

<script>
    import Condir from './condir/condir.vue'
    export default {
        data(){
            return{
                list: [],
                tableHeight:600, // 表格高度
                tableLoading:false, //表格的加载中
                LogShow:false, //的显示与不显示
                hostId:'',
                fileName:'',
                type:''
            }
        },
        methods: {
            getHeight(){
                this.tableHeight = window.innerHeight-this.$refs['record-table'].$el.offsetTop-50;
            },
            hostseverConDir(){
               var params = {
                server:this.type
               }
                capis.hostseverConDir(this.hostId,params,{})
                .then(data=>{
                    if(data.status == 'success'){
                        //对数据进行赋值
                        this.list = data.res.data;
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '提示',
                        message: err,
                        type: 'warning'
                    });
                })
            },
            freshData(data)
            {
                // 子组件给父组件传的至
                if(data && data.type == 'cancel')
                {
                    // 子组件取消操作,隐藏弹出层
                    
                }
                else if(data && data.type == 'update')
                {
                    //子组件点击了确认操作， 编辑的更新操作
                    this.hostseverConDir();
                }
                else if(data && data.type == 'add')
                {
                    //子组件点击了确认操作， 新增
                    //把页面赋值为1，然后进行更新操作
                    this.pagenation.page = 1;
                    // this.getRoleIndexList();
                }
            },
            LogInfo(data)
            {
                this.LogShow = true ;
                this.fileName = data.row.fileName;

            },
        },
        created(){
            this.type = this.$route.query.type ? this.$route.query.type:''
            this.hostId = this.$route.query.hostId ? this.$route.query.hostId:''
            this.name = this.$route.query.name ? this.$route.query.name:''
            this.deviceName = this.$route.query.deviceName ? this.$route.query.deviceName:''
            this.deviceHost = this.$route.query.deviceHost ? this.$route.query.deviceHost:''
            this.roleId = this.$route.query.roleId ? this.$route.query.roleId:''
            this.hostseverConDir();
        },
        components:{
            Condir,
        },
    }
</script>

<style scoped lang="scss">
.tac{
    text-align:center; /* 文字居中 */
    .tac-item{
        float:right;
        margin-right: 30px;
    }
}
.el-table .warning-row {
    background: oldlace;
  }

  .el-table .success-row {
    background: #f0f9eb;
  }
</style>


