* @LastEditTime: 2022-05-24 10:41:09
-->
<template>
    <div class="iptableinfo">
        <div class="top-tilte">
            <div class="serviceinfo-left">
                <div class="serviceinfo-title">
                    <p class="serviceinfo-back"></p>
                    <p class="serviceinfo-border"></p>
                    <p class="serviceinfo-tit">{{name}}</p>
                </div>
            </div>
            <div class="right">
                <div class="top-right-title">
                    {{deviceName}}
                </div>
                <div class="button">
                    <div class="top-right-title-adrss">
                        {{deviceHost}}
                    </div>
                    <el-dropdown trigger="click" @command="handleCommand" class="button-button">
                        <el-button type="primary" size="mini">
                            操作<i class="el-icon-arrow-down el-icon--right"></i>
                        </el-button>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item command="start">启动</el-dropdown-item>
                            <el-dropdown-item command="stop">停止</el-dropdown-item>
                            <el-dropdown-item command="restart">重启</el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </div>
            </div>
        </div>
        <div>
            <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" router>
                <el-menu-item index="/index/iptableInfo/ip" :route="{ path:'/index/iptableInfo/ip', query: {  hostId:hostId,type:type,name:name,deviceName:deviceName,roleId:roleId,deviceHost:deviceHost } }">端口规则</el-menu-item>
                <el-menu-item index="/index/iptableInfo/iprule" :route="{ path:'/index/iptableInfo/iprule', query: {hostId:hostId,type:type,name:name,deviceName:deviceName,roleId:roleId,deviceHost:deviceHost} }">IP规则</el-menu-item>
            </el-menu>
            <router-view></router-view>
        </div>
        <el-dialog
            title="提示"
            :visible.sync="dialogVisible"
            width="30%"
            :before-close="handleClose">
            <span>确定要执行此操作吗？</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="hostseverOperation">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    export default{
        data(){
            return{
                activeIndex:'/index/iptableInfo/ip"',
                hostId:'',
                roleId:'',
                type:'',
                name:'',
                deviceName:'',
                deviceHost:'',
                dialogVisible: false,
                command:null,
                serveInfo:{},
            }
        },
        methods:{
            // hostseverInfo(){
            //     var params = {
            //         hostId:this.$route.query.hostId,
            //         roleId:this.$route.query.roleId
            //     }
            //     capis.hostseverInfo(params,{})
            //     .then(data=>{
            //         this.tableLoading = false;
            //         if(data.status == 'success'){
            //             //对数据进行赋值
            //             let serveInfo = data.res.data;
            //             serveInfo.time = serveInfo.time>0?ctool.getYMDHMS(serveInfo.time):"-";
            //             // }
            //             this.serveInfo = data.res.data;
            //         }else{
            //             this.$notify({
            //                 title: '提示',
            //                 message: data.des,
            //                 type: 'warning'
            //             });
            //         }
                
            //     })
            //     .catch(err=>{
            //         this.tableLoading = false;
            //         this.$notify({
            //             title: '提示',
            //             message: '操作失败',
            //             type: 'warning'
            //         });
            //     })
            // },
            handleCommand(command) {
                this.dialogVisible=true;
                this.command = command;
            },
            handleClose(done) {
                this.$confirm('确认关闭？')
                .then(_ => {
                    done();
                })
                .catch(_ => {});
            },
            hostseverOperation(){
                var params = {
                    act:this.command,
                    service:this.type,
                    hostId:this.hostId
                }
                capis.hostseverOperation(params,{})
                .then(data=>{
                    this.dialogVisible = false;
                    if(data.status == 'success'){
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'success'
                        });
                    }
                    else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
            }
        },
        mounted(){
        },
        components:{
        },
        computed:{
            
        },
        created()
        {
            // this.hostseverInfo();
            this.type = this.$route.query.type ? this.$route.query.type:''
            this.hostId = this.$route.query.hostId ? this.$route.query.hostId:''
            this.name = this.$route.query.name ? this.$route.query.name:''
            this.deviceName = this.$route.query.deviceName ? this.$route.query.deviceName:''
            this.deviceHost = this.$route.query.deviceHost ? this.$route.query.deviceHost:''
            this.roleId = this.$route.query.roleId ? this.$route.query.roleId:''
        }
    }
</script>
 
<style scoped lang="scss">
.iptableinfo{
        box-sizing: border-box;
        background-color: #fff;
        box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
        border-radius: 8px;
        padding: 20px 20px;
        .top-tilte{
            display: flex;
            justify-content: space-between;
            .serviceinfo-title{
                display: flex;
                font-size: 20px;
                font-weight: 600;
                .serviceinfo-back{
                    i{
                        font-size: 30px;
                        color: #ceceee;
                    }
                }
                .serviceinfo-border{
                    width: 10px;
                    height: 26px;
                    background-color: #5e5ec6;
                    border-radius: 5px;
                    margin-left: 20px;
                }
                .serviceinfo-tit{
                    margin-left: 20px;
                }
            }
            .top-right-title{
                font-size: 20px;
                font-weight: 600;
            }
            .top-right-title-adrss{
            padding-top: 5px;
            font-size: 14px;
            font-weight: 300;
            color: #999;
            letter-spacing:1px
            }
        }
    }
    .button{
        margin-top: 5px;
        display: flex;
        justify-content: space-between;
    }
    .button-button{
            padding-left: 10px;
    }
 </style>


