<!--
 * @Descripttion: 设备管理修改规则
 * @version: 1.0
 * @Author: GaoX
 * @Date: 2022-04-11 23:06:31
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-05-16 15:30:07
-->
<template>
    <div class="warning-add">
        <el-table
                 ref="record-table"
                 :data="list"
                 style="width: 100%"
                 stripe
                 :max-height="500"
                 highlight-current-row
                 v-loading="tableLoading"
             >
                 <el-table-column
                     type="index"
                     width="50">
                 </el-table-column>
                 <el-table-column
                 label="名称"
                  prop="title"
                 sortable
                 show-overflow-tooltip
                 >
                 </el-table-column>
                 <el-table-column
                 label="发送类型"
                  prop="host"
                 sortable
                 >
                 <template slot-scope="scope">
                     <p v-if="scope.row.sendType == 1">电话</p>
                     <p v-else-if="scope.row.sendType == 2">短信 </p>
                     <p v-else-if="scope.row.sendType == 3">邮件 </p>
                     <p v-else-if="scope.row.sendType == 4">电话+短信</p>
                     <p v-else-if="scope.row.sendType == 5">电话+邮件</p>
                     <p v-else-if="scope.row.sendType == 6">短信+邮件</p>
                     <p v-else-if="scope.row.sendType == 7">电话+短信+邮件</p>
                 </template>
                 </el-table-column>
                 <el-table-column
                 label="服务器类型"
                 sortable
                 >
                 <template slot-scope="scope">
                        <p v-if="scope.row.type == 1">centos服务器</p>
                 </template>
                 </el-table-column>
                 <el-table-column
                 label="更新时间"
                  prop="updated_at"
                 sortable
                 fixed="right"
                 >
                 </el-table-column>
                 <el-table-column
                 label="操作"
                 fixed="right"
                 >
                <template slot-scope="scope">
                    <el-switch
                        v-model="scope.row.status"
                        active-color="#13ce66"
                        inactive-color="#ff4949"
                        :active-value='1'
                        :inactive-value='2'
                        @change="changeStatus(scope)"
                        >
                    </el-switch>
                </template>
                 </el-table-column>
             </el-table>
    </div>
 </template>
 <script>
     export default{
         props:{
             hostId:{
                 default:''
             }
         },
         data(){
             return{
               list:null,//所有的规则
               tableLoading:false
             }
         },
         components:{
             
         },
         created(){
             //获取到所有的数据
             this.getNotificationSettingListEnable();
             // this.getHostNotificationList();
             
         },
         methods: {
             getNotificationSettingListEnable(){
                 this.tableLoading=true;
                 var params = {};
                 capis.getNotificationSettingListEnable(params,{})
                 .then(data=>{
                     this.tableLoading = false;
                     if(data.status == 'success'){
                         //对数据进行赋值
                         this.list = data.res.data;
                         //获取到当前服务器下的数据
                         this.getHostNotificationList();
                     }else{
                         this.$notify({
                             title: '提示',
                             message: data.des,
                             type: 'warning'
                         });
                     }
                 
                 })
                 .catch(err=>{
                     this.tableLoading = false;
                     this.$notify({
                         title: '提示',
                         message: '操作失败',
                         type: 'warning'
                     });
                 })
             },
             getHostNotificationList(){
                 //获取到当前主机下已分配的规则
                  var params = {
                      hostId:this.hostId,
                      notificationId:this.notificationId
                  };
                 capis.getHostNotificationList(params,{})
                 .then(data=>{
                     this.tableLoading = false;
                     if(data.status == 'success'){
                         //对数据进行赋值
                         if(data.res.data && data.res.data.length>0)
                         {
                             var json = data.res.data;
                             for(var i=0; i<json.length; i++)
                             {
                                 for(var j=0; j<this.list.length; j++)
                                 {
                                     
                                     if(this.list[j].id == json[i].notificationId)
                                     {
                                         this.list[j].status = 1;
                                         break;
                                     }
                                 }
                             }
                         }
                     }else{
                         this.$notify({
                             title: '提示',
                             message: data.des,
                             type: 'warning'
                         });
                     }
                 
                 })
                 .catch(err=>{
                     this.tableLoading = false;
                     this.$notify({
                         title: '提示',
                         message: '操作失败',
                         type: 'warning'
                     });
                 })
             },
             changeStatus(data){
                 //增加和移除规则
                 if(data.row.status == 1)
                 {
                     var params = {
                         hostId:this.hostId,
                         notificationId:data.row.id
                     };
                     capis.hostNotificationSave(params,{})
                     .then(data=>{
                         this.tableLoading = false;
                         if(data.status == 'success'){
                             //对数据进行赋值
                             if(data.res.data && data.res.data.length>0){
                                 var json = data.res.data;
                                 for(var i=0; i<json.length; i++)
                                 {
                                     for(var j=0; j<this.list.length; j++)
                                     {
                                         if(this.list[j].id == json[i].id)
                                         {
                                             this.list[j].status = 1;
                                         }
                                     }
                                 }
                             }
                         }else{
                             this.$notify({
                                 title: '提示',
                                 message: data.des,
                                 type: 'warning'
                             });
                         }
                     })
                 }
                 else if(data.row.status == 2)
                 {
                     var params = {
                         hostId:this.hostId,
                         notificationId:data.row.id
                     };
                     capis.hostNotificationEdit(params,{})
                     .then(data=>{
                         this.tableLoading = false;
                         if(data.status == 'success'){
                             //对数据进行赋值
                             if(data.res.data && data.res.data.length>0){
                                 var json = data.res.data;
                                 for(var i=0; i<json.length; i++)
                                 {
                                     for(var j=0; j<this.list.length; j++)
                                     {
                                         if(this.list[j].id == json[i].id)
                                         {
                                             this.list[j].status = 1;
                                         }
                                     }
                                 }
                             }
                         }else{
                             this.$notify({
                                 title: '提示',
                                 message: data.des,
                                 type: 'warning'
                             });
                         }
                     })
                 }
                 
             }
         }
     }
 </script>
 <style scoped lang="scss">
    .warning-add{
        padding: 0 80px;
    }
 </style>


